import Vue from "vue";
import VueRouter from "vue-router";
import Layout from "@/views/Layout";

Vue.use(VueRouter);

const routes = [
    {
        path: "/home",
        component: Layout,
        meta: {
            title: '後台站',
            auth: true
        },
        children: [
            {
                path: "/customer",
                component: () => import('../views/Customer'),
                name: 'Customer'
            },
            {
                path: "/service",
                component: () => import('../views/Service'),
                name: 'Service'
            },
            {
                path: "/service/:id",
                component: () => import('../views/Msgrecord'),
                name: 'Msgrecord'
            },
        ]
    },
    {
        path: "/",
        component: () => import('@/views/Login'),
        name: "Login",
        meta: {
            title: '登入',
            auth: false
        }
    },
];


const router = new VueRouter({
    mode: 'history',
    base: '/',
    routes,
});

router.beforeEach((to, from, next) => {
    //根據字段判斷路由是否過濾
    if (to.matched.some(record => record.meta.auth)) { //第一層帳密驗證
        const authToken = localStorage.getItem('auth_token')
        if (authToken !== '' || authToken !== null || authToken !== "undefined") {
            next(); // 往下繼續執行
        } else {
            alert('請先登入')
            // 轉跳到 login page
            next('/login');
        }
    } else {
        next(); // 往下繼續執行
    }
});

export default router;