<template>
  <div id="app" @click="clicked">
    <v-app>
      <router-view></router-view>
    </v-app>
  </div>
</template>

<script>
export default {
  name: "App",

  data: () => ({
    lTime: new Date().getTime(), // 最後一次點擊的時間
    cTime: new Date().getTime(), //當前時間
    tOut: 10 * 60 * 10000, //超時時間10min
  }),

  mounted() {
    window.setInterval(this.tTime, 1000);
    // window.setInterval(this.checkToken, 60000);
  },

  methods: {
    clicked() {
      this.lTime = new Date().getTime(); //當界面被點擊更新點擊時間
    },

    tTime() {
      this.cTime = new Date().getTime();
      if (this.cTime - this.lTime > this.tOut) {
        if (localStorage.getItem("auth_token")) {
          alert("你已被登出");
          localStorage.removeItem("account");
          this.$router.push({ name: "Login" });
        }
      }
    },
  },
};
</script>
