        </v-list-item>

        <v-list-group :value="false">
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title>顧問管理</v-list-item-title>
            </v-list-item-content>
          </template>

          <div
            class="d-flex"
            v-for="item in consultant"
            :key="item.name"
            @click="name = item.title"
          >
            <v-list-item link>
              <v-list-item-icon>
                <v-icon v-text="item.icon"></v-icon>
              </v-list-item-icon>

              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item>
          </div>
        </v-list-group>

        <v-list-group :value="false">
          <template v-slot:activator>
