            v-for="item in advertise"
            :key="item.name"
            @click="name = item.title"
          >
            <v-list-item link>
              <v-list-item-icon>
                <v-icon v-text="item.icon"></v-icon>
              </v-list-item-icon>

              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item>
          </div>
        </v-list-group>
