<script>
export default {
  data: () => ({
    drawer: false,
    name: "儀錶板",
    consultant: [
      {
        title: "顧問列表",
        to: "",
        icon: "mdi-account-supervisor",
      },
      {
        title: "名片列表",
        to: "",
        icon: "mdi-card-account-details-outline",
      },
      {
        title: "合約管理",
        to: "",
        icon: "mdi-newspaper-variant-outline",
      },
    ],
    customer: [
      {
        title: "客戶列表",
        to: "Customer",
        icon: "mdi-account-supervisor",
      },
      {
        title: "客服對話管理",
        to: "Service",
        icon: "mdi-card-account-details-outline",
      },
    ],
    advertise: [
      {
        title: "廣告機列表",
        to: "",
        icon: "mdi-account-supervisor",
      },
      {
        title: "新增廣告機",
        to: "",
        icon: "mdi-card-account-details-outline",
      },
      {
        title: "機器設備型號列表",
        to: "",
        icon: "mdi-card-account-details-outline",
      },
    ],
  }),

